/* //set
//h1은 모든 라우터에 들어간 제목들
//h2는 라우터 안에서 drawing 컴포넌트의 제목들 pencil, oil 등등
//h3는 TA라우터의 love, hate들 
//p는 about과 text의 글들
//span은 TA의 i love, i hate */

/* Global tags */
:root {
  /* color */
  --color-ashsky: #86beb3;
  --color-blank: #00000c;
  --color-pink: #fecaf0;
  --color-beige: #eee7d8;
  --color-sky: #2fa5e2;
  --color-blue: #0004e2;
  --color-deeppurple: #4d0747;
  /* Font size */
  --font-max: 32px;
  --font-big: 24px;
  --font-medium: 20px;
  --font-regular: 16px;
  --font-small: 14px;
  --font-ssmall: 13px;
  /* Font weight */
  --weight-Extra-bold: 800;
  --weight-bold: 700;
  --weight-semi-bold: 600;
  --weight-regular: 400;
  /* Font family */
  --typo-eng: "Source Code Pro", monospace;
  --typo-kor: "Noto Serif KR", serif;
}

/* Univeral tags */
* {
  box-sizing: border-box;
}
body {
  margin: 0px;
}

/* Each tags */
h1 {
  margin: 0px;
}
h2 {
  font-size: 14px;
  font-size: var(--font-small);
  font-weight: 700;
  font-weight: var(--weight-bold);
  margin: 0px;
}
h3 {
  font-size: 14px;
  font-size: var(--font-small);
  font-weight: 600;
  font-weight: var(--weight-semi-bold);
  margin: 0px;
}
ul {
